export default {
	data() {
		return {
			classify: null,
			currentCategoryId: null,
			goods: [],
			keywords:"",
			stopLoad:false,
			page:1
		}
	},
	onShow() {
		this.classify = uni.getStorageSync('classify')
		this.currentCategoryId = this.classify.category[0].id
		uni.setNavigationBarTitle({
			title: this.classify.name
		})
		this.getCategoryList()
	},
	methods: {
		toDetail(goods_id, shop_id) {
			uni.navigateTo({
				url: "../goodsDetail/goodsDetail?id=" + goods_id + "&shop_id=" + shop_id
			})
		},
		toShop(){
			uni.navigateTo({
				url:"../search/search"
			})
		},
		chooseCategory(id) {
			this.currentCategoryId = id
			this.getCategoryList()
		},
		getCategoryList() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsList",
				data: {
					token: uni.getStorageSync('token'),
					pages: 1,
					cat_id: this.currentCategoryId
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.goods = res.data.data
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		loadMoreGoods(){
			if(this.stopLoad){
				return
			}
			this.page++
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsList",
				data: {
					token: uni.getStorageSync('token'),
					pages: this.page,
					cat_id: this.currentCategoryId
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if(res.data.data.length==0){
							this.stopLoad = true
							return
						}
						for (var i = 0; i < res.data.data.length; i++) {
							this.goods.push(res.data.data[i])
						}
						return
					}
					this.showToast(res.data.msg)
				}
			})
		}
	},
	//页面触底加载更多精选商品
	onReachBottom() {
		console.log('触底了')
		this.loadMoreGoods()
	}
}
